import React, { useState } from "react";
import { Button, CircularProgress, Grid, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import "react-simple-keyboard/build/css/index.css";

const useStyles = makeStyles({
  backdrop: {
    zIndex: 10000,
    color: "#fff"
  },
  buttonContainer: {
    display: "flex",
    width: "100%",
    justifyContent: "space-around",
    marginTop: "56px"
  },
  connecting: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    "& h4": {
      marginBottom: "24px"
    }
  },
  container: {
    display: "flex",
    flexDirection: "column"
  },
  header: {
    display: "flex",
    alignItems: "center"
  },
  item: {
    padding: (props) => (props.settingsScreen ? "10px" : "30px 15px"),
    fontSize: (props) => (props.settingsScreen ? "18px" : "24px"),
    display: "flex",
    justifyContent: "space-between"
  },
  keyboard: {
    position: "fixed",
    bottom: 0,
    left: 0,
    right: 0,
    color: "#000"
  }
});

const ResetPrinter = ({
  setStep,
  setShowSnack,
  setSnackMessage,
  settingsScreen
}) => {
  const classes = useStyles();
  const type = "configure";
  const [rebootMessage, setRebootMessage] = useState(false);

  const handleReset = () => {
    console.log("reset");
    localStorage.clear();
    setRebootMessage(true);
    setTimeout(() => {
      fetch(`http://localhost:8990/reboot`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        }
      });
    }, 3000);
  };

  return (
    <Grid
      container
      item
      spacing={6}
      direction="column"
      alignItems="center"
      className={classes.container}
    >
      {type === "configure" && (
        <div>
          <Grid
            container
            item
            spacing={6}
            direction="column"
            alignItems="center"
            style={{ paddingTop: 140 }}
          >
            <Typography variant="h4" style={{ textAlign: "center" }}>
              Warning!
            </Typography>
            <Typography variant="h6" style={{ textAlign: "center" }}>
              This will reset your printer to factory settings.
            </Typography>
            <Typography variant="h6" style={{ textAlign: "center" }}>
              All gcode and stl files will be preserved.
            </Typography>
          </Grid>
          <Grid
            container
            item
            spacing={6}
            direction="column"
            alignItems="center"
            style={{ marginTop: 100 }}
          >
              {rebootMessage ? (
                <Grid
                  container
                  direction="column"
                  alignItems="center"
                >
                <Grid item>
                  <CircularProgress className={classes.button} color="secondary" />
                </Grid>
                <Grid item>
                  <Typography
                    variant="h5"
                    style={{ marginTop: 60, textAlign: "center" }}
                  >
                    Rebooting printer to apply changes, this will take approximately 2
                    minutes.
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography
                    variant="h5"
                    style={{ marginTop: 60, textAlign: "center" }}
                  >
                    DO NOT TURN OFF PRINTER UNTIL AFTER IT REBOOTS!
                  </Typography>
                </Grid>
                </Grid>
              ) : (
                <Grid item>
                  <Button
                    color="secondary"
                    variant="contained"
                    className={classes.button}
                    style={{ marginTop: 10 }}
                    onClick={() => {
                      handleReset();
                    }}
                  >
                    Reset
                  </Button>
                </Grid>
              )}
          </Grid>
        </div>
      )}
    </Grid>
  );
};

export default ResetPrinter;
