import React, { useEffect, useState } from "react";
import { AppBar } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import TabList from "../Common/Tabs/TabList";
import Print from "../Print/Print";
import Control from "../Control/Control";
import Menu from "../Menu/Menu";
import Tune from "../Tune/Tune";

import "./Home.css";
const useStyles = makeStyles((theme) => ({
  appBar: {
    top: "auto",
    bottom: 0,
    textAlign: "center",
    padding: 0
  }
}));
const tabList = ["Print", "Control", "Tune", "Menu"];
const Home = ({ lastMessage, mode, setActiveMode }) => {
  const classes = useStyles();
  const [activeTab, setActiveTab] = useState("Print");
  const [disabledNav, setDisabledNav] = useState(false);

  React.useEffect(() => {
    if (
      typeof lastMessage !== "undefined" &&
      lastMessage &&
      typeof lastMessage.data !== "undefined" &&
      lastMessage.data.length > 1
    ) {
      let json = JSON.parse(lastMessage.data.substring(1));

      if (json[0].hasOwnProperty("current") && json[0].current.progress) {
        if(
          json[0].current.progress &&
          json[0].current.job &&
          json[0].current.job.file.name &&
          json[0].current.progress.completion < 100 &&
          json[0].current.state &&
          json[0].current.state.text !== "Operational"
        ) {
          setActiveTab('Print');
          setDisabledNav(true);
        } else {
          setDisabledNav(false);
        }
      }

    }
  }, [lastMessage]);

  const handleTabClick = (event, val) => {
    setActiveTab(tabList[val]);
  };
  const Components = {
    Print: <Print lastMessage={lastMessage} setDisabledNav={setDisabledNav} />,
    Control: <Control lastMessage={lastMessage} />,
    Menu: <Menu lastMessage={lastMessage} />,
    Tune: <Tune lastMessage={lastMessage} />
  };
  return (
    <div className="home-container">
      <AppBar
        position="fixed"
        color="primary"
        className={classes.appBar}
        disabled={true}
      >
        <TabList
          tabList={tabList}
          handleTabClick={handleTabClick}
          activeTab={tabList.indexOf(activeTab)}
          disabledNav={disabledNav}
        />
      </AppBar>
      <div className="component-container">{Components[activeTab]}</div>
    </div>
  );
};

export default Home;
